.head {
  padding: 0;
}

h1{
  font-size: 3rem;
}

.search-box {
  width: 100%;
}

.input {
  margin-top: 20px;
  font-size: 32px;
}
.images {
  padding-top: 50px;
}

.image-list {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 0 10px;
  grid-auto-rows: 10px;
}

.image-list img {
  width: 250px;
  grid-row-end: span 2;
}
.head-back {
  background: url(/photo-gallery/static/media/back.40574d35.jpg);
  background-size: cover;
  padding-top: 150px;
  padding-bottom: 150px;
  background-position-y: 30%;
}
